@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Advent+Pro:wght@400;600;700&display=swap);
.card{flex-grow:2;display:flex;flex-direction:column;width:18em;margin:0 auto;margin-top:60px}.card__title{font-size:1rem;letter-spacing:0.1em;text-transform:uppercase;font-weight:600;margin-bottom:3.33em}.card__img{margin-bottom:2.66em}.card__descriptions{display:flex;align-items:center;justify-content:flex-end}.card__text{text-transform:uppercase;text-align:right;font-weight:500;font-size:14px;line-height:17px}.card__text--lighter{color:var(--secondary-color);text-transform:none;font-size:10px;line-height:12px}.card__text--bigger{font-weight:bold;font-size:36px;line-height:43px;text-align:right;letter-spacing:0.095em;margin-left:2rem}

.details__input{height:0;width:0;visibility:hidden}.details__label{position:relative;margin:0 auto;padding:0.3em;letter-spacing:0.15em;text-transform:uppercase;line-height:14px;margin-top:auto;cursor:pointer;transition:all 1000ms ease-out}.details__label::after{content:"";position:absolute;left:50%;margin-left:-25px;background-color:#000000;width:50px;height:1px;bottom:0}.details__label:hover{-webkit-animation:fonter 1000ms infinite ease-out;animation:fonter 1000ms infinite ease-out}.details__input:checked ~ .details__label{transform:translateY(-100px)}.details__input:checked ~ .details__container{margin-bottom:5rem}.details__container{position:absolute;width:20em;bottom:0%;left:50%;margin-left:-120px;margin-bottom:-120px;transition:all 1000ms ease-out;overflow:hidden}.details__container--row{display:flex;justify-content:space-between}.details__container--margin-top{margin-top:2rem}.details__numbers{display:flex;flex-direction:column;font-weight:500;font-size:1.35rem;line-height:18px;text-align:right}.details__description{line-height:14px;font-size:1rem;text-transform:capitalize;color:var(--secondary-color)}@-webkit-keyframes fonter{0%{font-style:italic}50%{font-family:monospace;font-style:bold}100%{font-family:bookman}}@keyframes fonter{0%{font-style:italic}50%{font-family:monospace;font-style:bold}100%{font-family:bookman}}

.input{position:relative;display:flex;justify-content:space-between;align-items:center;width:70%;max-width:300px;margin:0 auto;margin-top:40px}.input__container{display:flex;align-items:center}.input__text{background-image:linear-gradient(transparent 50%, var(--secondary-color-light) 50%, var(--secondary-color-light) 100%);padding-right:1rem;text-transform:uppercase;line-height:1.66rem;letter-spacing:0.15em;margin-left:1.3rem}.input__search{position:absolute;font-family:"Advent Pro", sans-serif;text-transform:uppercase;font-weight:700;letter-spacing:0.1em;font-size:1.2rem;line-height:1.66rem;background-color:transparent;border:none;width:100%;padding-left:11.7rem;padding-right:2rem}.input textarea:focus,.input input:focus,.input button:focus{outline:none}.input__btn{z-index:1;cursor:pointer;background-color:transparent;border:none;transition:ease-out 200ms}.input__btn:hover{transform:translateX(5px)}

.unit-toggle{display:flex;justify-content:center;align-items:center}.unit-toggle__input{height:0;width:0;visibility:hidden}.unit-toggle__input:checked+.unit-toggle__label{color:var(--base-color);font-weight:600;font-size:14px}.unit-toggle__label{font-family:"Montserrat", sans-serif;padding:0.5rem;letter-spacing:0.1em;line-height:15px;color:var(--secondary-color);cursor:pointer;transition:ease-out 200ms}.unit-toggle__element{color:var(--secondary-color)}

.loader{display:inline-block;margin-top:60px;width:30px;height:30px;border:1px solid var(--secondary-color);-webkit-animation:loader 2s infinite ease;animation:loader 2s infinite ease}.loader__inner{vertical-align:top;display:inline-block;width:100%;background:linear-gradient(180deg, #eede91 0%, #bedde2 100%);-webkit-animation:loader__inner 2s infinite ease-in;animation:loader__inner 2s infinite ease-in}@-webkit-keyframes loader{0%{transform:rotate(0deg)}25%{transform:rotate(180deg)}50%{transform:rotate(180deg)}75%{transform:rotate(360deg)}100%{transform:rotate(360deg)}}@keyframes loader{0%{transform:rotate(0deg)}25%{transform:rotate(180deg)}50%{transform:rotate(180deg)}75%{transform:rotate(360deg)}100%{transform:rotate(360deg)}}@-webkit-keyframes loader__inner{0%{height:0%}25%{height:0%}50%{height:100%}75%{height:100%}100%{height:0%}}@keyframes loader__inner{0%{height:0%}25%{height:0%}50%{height:100%}75%{height:100%}100%{height:0%}}

.App{display:flex;flex-direction:column;align-items:center;min-height:100vh;height:100%;padding:5rem 0}

html{--base-color: hsl(0, 0%, 0%);--secondary-color: hsl(0, 0%, 62%);--secondary-color-light: hsla(0, 0%, 62%, 0.3);font-family:"Advent Pro", sans-serif;font-size:12px;color:hsl(0, 0%, 0%);color:var(--base-color)}*{box-sizing:border-box;margin:0;padding:0}

